<template>
  <el-container>
    <el-header style="padding: 0px 50px;">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select='handleSelect'>
        <el-menu-item index="1">图片</el-menu-item>
        <el-menu-item index="2">AI绘画教程</el-menu-item>
        <el-menu-item index="3">编程教程</el-menu-item>
        <el-menu-item index="4">关于</el-menu-item>
        <el-menu-item index="5">登录</el-menu-item>
      </el-menu>
    </el-header>
    <el-main style="margin: 0px 30px;">
      <el-row type="flex" justify="center">
        <div class="image_list">
          <div class="image_item" v-for="img in imgs" :key="img.name">
            <el-row type="flex" justify="center">
              <el-image
                style="border-radius: 5px;"
                :src="img.url"
                fit="contain"
                ></el-image>
            </el-row>
            <el-row type="flex" justify="center"><span>{{ img.name }}</span></el-row>
          </div>
        </div>
      </el-row>
      <el-row style="margin-top: 50px;"><el-divider >加载更多</el-divider></el-row>
    </el-main>
  </el-container>
</template>

<script>
import { getDeviceByUserId } from '../api/device.js'
export default {
  data () {
    return {
      input: '',
      form: {
        user: '',
        password: ''
      },
      imgs: [],
      activeIndex: '1'
    }
  },
  methods: {
    onSubmit () {
      console.log('登录')
    },
    onReset () {
      console.log('重置')
    },
    getDevice () {
      getDeviceByUserId({'userId': '1'}).then((response) => {
        var data = response.data
        console.log(data)
      })
    },
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    }
  },
  mounted () {
    for (var i = 1; i < 36; i++) {
      this.imgs.push({url: require('../img/' + i.toFixed() + '.png'), name: i.toFixed() + '.png'})
    }
    // getDeviceByUserId({'userId': '1'}).then((response) => {
    //   var data = response.data
    //   console.log(data)
    // })
  }
}
</script>

<style scoped>

.toolbox{
  display: inline-block;
  display: inline-flex;
  justify-content: center;
}
.header_pBtn {
  display: inline-block;
  padding: 5px 15px;
  margin: 10px;
  border-radius: 15px;
  font-size:medium;
  cursor:pointer;
  height: fit-content;
  width: fit-content;
}
.image_list{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  justify-content: flex-start;
}
.image_item{
  width: 24%;
  margin: 0.5%;
}
.header_pBtn:hover{
  background:rgba(180, 203, 219, 0.232);
}
</style>



